import React from "react";
import ReactApexChart from "react-apexcharts";
import { reArrangeSeriesData } from "../../../../../utils";

class ChartDataBasedOnUrl extends React.Component {
  constructor(props) {
    super(props);

    const e = reArrangeSeriesData(props.data)

    this.state = {
      series: [{
        data: e.labels
      }],
      options: {
           colors:  ['#546FED'],
           chart: {
            height: 350,
            type: props.type,
            animations: {
              enabled: true,
              easing: 'bar',
              dynamicAnimation: {
                speed: 1000
              }
            },
            toolbar: {
              show: false
            },
            zoom: {
              enabled: false
            }
          },
        plotOptions: {
            bar: {
              borderRadius: 8,
              borderRadiusApplication: 'end',
              horizontal: true,
              barHeight: 18,  // Controls the height of the bar
            } 
          },
          dataLabels: {
            enabled: false,
          },
          stroke: {
            curve: 'smooth'
          },
       legend: {
            show: true,
            position: 'top',
            itemMargin: {
              horizontal: 20,
              vertical: 0
            },
          },
          
         tooltip: {
            custom: function({ series, seriesIndex, dataPointIndex, w }) {
              console.log(series,'seriesseriesseriesseries',seriesIndex,'dataPointIndexdataPointIndex',dataPointIndex,w.config.xaxis.categories)

              return `
                <div style="padding: 10px; background-color: #fff; border: 1px solid #ccc; border-radius: 5px;">
                   <div>Series: ${series[seriesIndex]}</div><br/>
                   <strong>Data Point Info</strong><br/>
                   <em>Value: ${series[seriesIndex][dataPointIndex]}</em>
                </div>`;
            }
          },
        annotations: {
          points: [
            {
              x:  e.labels[e.maxIndex], // Dynamically set based on last category
              y:  e.newSeries[e.maxIndex], // Last data point
              marker: {
                size: e.newSeries.length > 0 ? 10 : 0, // Highlight the last marker
                fillColor: "#546FED",
                strokeColor: "#fff",
                offsetY: 45, // Move the label higher to avoid overlap
               // offsetX: -8, // Move the label higher to avoid overlap
                strokeWidth: 2,
                shape: "circle",
                cssClass: "screen-marker", // Apply custom CSS class for blinking effect
              },
              label: {
                borderColor: "#546FED",
                offsetY: 40, // Move the label higher to avoid overlap
                //offsetX: -70, // Move the label higher to avoid overlap
                style: {
                  color: "#546FED",
                  background: "transparent", // Make label background transparent
                  zIndex: 0, // Lower z-index to ensure marker is on top
                  flexWrap: 'wrap',
                  wordWrap: 'break-word' 
                },
                text: e.newSeries[e.maxIndex] ?  e.newSeries[e.maxIndex] + ' '+  e.series[e.maxIndex]+ ' Hours' : '',
               },
               className: 'custom-label-class', // Add custom class for the entire label
            }],
          xaxis: [{
            x: e.labels[e.maxIndex],
            borderColor: '#000',
            label: {
              borderColor: '#fff',
              style: {
                color: '#000',
                background: '#fff',
              },
            }
          }],
        },
        dataLabels: {
          enabled: false
        },
        xaxis: {
          categories: e.newSeries,
        },
        grid: {
          xaxis: {
            lines: {
              show: true
            }
          }
        },
        yaxis: {
          reversed: false,
          axisTicks: {
            show: true
          },
          labels: {
            rotate: -35, // Rotate the labels by -35 degrees
            formatter: function (value) {
              const maxLength = 12; // Define your max length
              return value.length > maxLength ? value.substring(0, maxLength) + '...' : value;
            }
          }
        }
      },
    };
  }

  render() {
    return (
      <div>
        <div id="chart">
           <ReactApexChart options={this.state.options} series={this.state.series} type="bar" height={350} />
        </div>
      </div>
    );
  }
}

export default ChartDataBasedOnUrl